import React, { Component } from 'react';
import {
	Box,
	Center,
    Flex,
    Spacer,
    HStack,
    Text,
  } from "@chakra-ui/react"

import HeaderMenuButton from './HeaderMenuButton';
import useWindowSize from '../../hooks/useWindowSize'

function Header(props) {

	const menu_items = {
		About: {
			width:"100px", 
			content:[["Careers", "./"], ["FAQ", "./"]]
		},
		Docs: {
			width:"100px", 
			content:[["White Paper", "./"], ["Tokenomics", "./"], ["LAP AMM Design", "./"]]
		},
	};

	const menuItems = Object.keys(menu_items).map((item) =>
		<HeaderMenuButton
			key={item} 
			name={item} 
			width={menu_items[item]["width"]} 
			content={menu_items[item]["content"]}
		/>
	);	

	return (
		<Center 
			w="100%" 
			bg="#1F1F21"
			position="relative"
        >
            <Box
                zIndex="4"  
                height="78px"
                maxWidth="928px"
                w="100%"
                // bg="#000000"
                margin="24px 24px 0 24px"
                borderBottom="3px solid rgba(255, 255, 255, 0.1)"
            >
                <Flex margin="0 0 0 10px">
                    <Box width="165px" height="54px" position="relative">
                        <Text top="32px" position="absolute" fontSize="24px" lineHeight="24px" color="#FFFFFF" fontWeight="500">
                            Hang Zhang
                        </Text>
                    </Box>
                    <Spacer />
                    {/* <Box zIndex="5" height="54px" position="relative">
                        <HStack >
                            {menuItems}
                        </HStack>
                    </Box> */}
                </Flex>
            </Box >
		</Center>
	)
}

export default Header;