import React, { Component } from 'react';
import {
	Box,
	Center,
    Flex,
    Spacer,
    HStack,
    VStack,
    Image,
    Text,
    Wrap,
    WrapItem,
    Link,
} from "@chakra-ui/react"

import useWindowSize from '../../hooks/useWindowSize'
import { XIcon, GIcon, EmailIcon, ZhihuIcon, GithubIcon, IGIcon, ColabIcon } from '../../icons'

function Intro(props) {

    const { width } = useWindowSize();
    const lfs = props.fontSizes['large'];
    const sfs = props.fontSizes['small'];

	return (
		<Center 
			w="100%" 
			bg="#1F1F21"
			position="relative"
        >
            <Box
                h="100%"
                w="100%"
                maxWidth="928px"
                // bg="#000000"
                // borderTop="3px solid rgba(255, 255, 255, 0.1)"
                margin="36px 24px 0 24px"
            >
                {/* <HStack> */}
                <Text position="relative" fontSize={lfs} lineHeight="24px" color="#FFFFFF" fontWeight="500" margin="0 0 10px 10px">
                    About
                </Text>
                <Box h="3px" w="100%" bg="rgba(255, 255, 255, 0.1)" />
                <Text fontFamily="Catamaran" position="relative" fontSize='18px' lineHeight="24px" color="#BBBBBB" fontWeight="400" margin="10px 10px 10px 10px" textAlign="justify">
                    Currently, my research is focused on machine learning and deep learning algorithms and theories, particularly for practical applications in medical image analysis and other areas requiring specialized domain knowledge. 
                    I am passionately dedicated to developing <i><span style={{ color: "#EE7D85" }}>Ultrahand</span></i>, a primitive neuralized tool that aims to bridge the gap between human and machine intelligence, improving clinical translation and being adaptable for complex challenges in various high-expertise fields.
                </Text>
            </Box >

		</Center>
	)
}

export default Intro;